import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {addPostItem, postItem} from "../slices/postSlice";

interface fetchPostMoreReturn {
    items: postItem[]
    count: number
}

interface changePostItem {
    id: number,
    text: string,
}

export const postAPI = createApi({
    reducerPath: 'postAPI',
    baseQuery: fetchBaseQuery({baseUrl: 'https://62ea46f9ad2954632588907e.mockapi.io/'}),
    tagTypes: ['Posts', 'Post'],
    endpoints: (build) => ({
        fetchAllPosts: build.query<fetchPostMoreReturn, string>({
            query: () => ({
                url: `posts`
            }),
            providesTags: result => ['Posts']
        }),
        fetchPost: build.query<postItem, number>({
            query: (id: number) => ({
                url: `posts/${id}`
            }),
            providesTags: result => ['Post']
        }),
        fetchSearchPosts: build.query<fetchPostMoreReturn, string>({
            query: (searchValue) => ({
                url: `posts`,
                params: {
                    search: searchValue
                }
            })
        }),
        addPost: build.mutation<fetchPostMoreReturn, addPostItem>({
            query: ({title, text, layoutDate, image}) => ({
                url: `posts`,
                method: 'POST',
                body: {title, text, layoutDate, image}
            }),
            invalidatesTags: ['Posts']
        }),
        deletePost: build.mutation<fetchPostMoreReturn, number>({
            query: (id: number) => ({
                url: `posts/${id}`,
                method: 'DELETE',
            }),
            invalidatesTags: ['Posts']
        }),
        changePost: build.mutation<fetchPostMoreReturn, changePostItem>({
            query: ({id, text}) => ({
                url: `posts/${id}`,
                method: 'PUT',
                body: {text}
            }),
            invalidatesTags: ['Post']
        }),
    })
})