import React, {FC, useCallback, useEffect, useState} from 'react';
import Text from "./Text";

interface PostTextProps {
    text: string
    setText: (text: string, prevText: string) => void
}

const PostText: FC<PostTextProps> = ({text, setText}) => {
    const [chart, setChart] = useState<number[]>([])
    const check = useCallback((text: string) => {
        const target = "\n";
        let pos = 0
        let result: number[] = []
        while (true) {
            let foundPos = text.indexOf(target, pos);
            if (foundPos === -1) break;
            result = [...result, foundPos]
            pos = foundPos + 1; // продолжаем со следующей позиции
        }
        setChart(result)
    }, [text])

    useEffect(() => {
        check(text)
    }, [text])


    return (
        <div>
            {text.slice(0, chart[0]) !== '' &&
                <Text text={text.slice(0, chart[0])} setText={setText}>{text.slice(0, chart[0])}<br/></Text>
            }
            {chart.map((t, index) =>
                <Text text={text.slice(t, chart[index + 1])}  setText={setText} key={index}>{text.slice(t, chart[index + 1])}<br/></Text>
            )}
        </div>
    );
};

export default PostText;