import "./root.scss"
import React from "react";
import ModalStory from "./components/ModalStory";
import ModalImage from "./components/ModalImage";
import {authSelect} from "./store/slices/authSlice";
import {useAppSelector} from "./store";
import Unauthorized from "./components/Unauthorized";
import Authorized from "./components/Authorized";
import {postSelect} from "./store/slices/postSlice";
import {storySelect} from "./store/slices/storySlice";
import {postAPI} from "./store/services/PostService";

function App() {
    const {showModal} = useAppSelector(storySelect)
    const {isAuth} = useAppSelector(authSelect)
    const {imageModal} = useAppSelector(postSelect)
    return (
        <div className={`App ${showModal === true ? 'block' : ''}`}>
            {showModal &&
                <ModalStory/>
            }
            {imageModal &&
                <ModalImage/>
            }
            {isAuth ? <Authorized/> : <Unauthorized/>}
        </div>
    );
}




export default App;
