import React, {FC, useState} from 'react';
import styles from '../../PostInfo.module.scss'
import {useAppSelector} from "../../../../../store";
import {authSelect} from "../../../../../store/slices/authSlice";

interface TextProps {
    children: React.ReactNode
    setText: (text: string, prevText: string) => void
    text: string
}

const Text: FC<TextProps> = ({children, setText, text}) => {
    const {isAuth} = useAppSelector(authSelect)
    const [updateMode, setUpdateMode] = useState(false)
    const [value, setValue] = useState('')
    const onChangeText = (text: string) => {
        setUpdateMode(true)
        setValue(text)
    }
    const onTextBlur = () => {
        setUpdateMode(false)
        setText(value, text)
    }
    return (
        <div>
            {isAuth ? updateMode ?
                    <textarea
                        autoFocus
                        onChange={(e) => setValue(e.target.value)}
                        rows={Math.ceil(value.length / 50)}
                        onBlur={onTextBlur}
                        value={value}
                        className={styles.textArea}
                    />
                    : <p
                        onDoubleClick={() => onChangeText(text)}
                        className={styles.desc}>
                        {children}
                    </p>
                : <p
                    className={styles.desc}>
                    {children}
                </p>
            }
        </div>
    );
};

export default Text;