import {createApi} from "@reduxjs/toolkit/query/react";
import {fetchBaseQuery} from "@reduxjs/toolkit/dist/query/react";
import {commentItem, commentPayload} from "../slices/commentSlice";
import {addPostItem} from "../slices/postSlice";

interface DeleteComment {
    id: number,
    postId: number
}


export const commentAPI = createApi({
    reducerPath: 'commentAPI',
    baseQuery: fetchBaseQuery({baseUrl: 'https://62ea46f9ad2954632588907e.mockapi.io/posts/'}),
    tagTypes: ['Comment'],
    endpoints: (build) => ({
        fetchCommentsByPostId: build.query<commentItem[], number>({
            query: (id: number) => ({
                url: `${id}/comments`
            }),
            providesTags: result => ['Comment']
        }),
        addComment: build.mutation<commentItem[], commentPayload>({
            query: ({id, text, layoutDate, avatar, author}) => ({
                url: `${id}/comments`,
                method: 'POST',
                body: {text, layoutDate, avatar, author}
            }),
            invalidatesTags: ['Comment']
        }),
        deleteComment: build.mutation<commentItem[], DeleteComment>({
            query: ({id, postId}) => ({
                url: `${postId}/comments/${id}`,
                method: 'DELETE',
            }),
            invalidatesTags: ['Comment']
        }),
    })
})